<template>
  <div id="login-section">
    <section
      class="title-login"
      :class="$vuetify.breakpoint.xsOnly ? 'mb-6' : 'mb-10'"
    >
      <div
        class="text-center font-weight-black"
        :class="$vuetify.breakpoint.lgAndUp ? 'display-1' : 'headline'"
      >
        Organize Your Activity
      </div>
      <div
        class="text-center font-weight-black"
        :class="$vuetify.breakpoint.lgAndUp ? 'title' : 'subtitle-1'"
      >
        Keep Focus and Always Smile
      </div>
    </section>
    <section class="form-login text-center mx-auto " :class="formWidth">
      <div class="form-view my-5 d-flex">
        <div class="form-input col-12" :class="animatedForm" style="padding: 0">
          <span
            class="form-label font-weight-medium"
            :class="$vuetify.breakpoint.xlOnly == 'title'"
          >
            Enter Your Name
          </span>
          <form
            @submit.prevent="submit"
            class="d-flex justify-space-between flex-column mt-6"
            style="height: 30vh"
          >
            <div class="form-input-gb">
              <v-text-field
                required
                label="Name"
                prepend-inner-icon="face"
                v-model="ownerLogin.username"
                :rules="rules"
                :color="$vuetify.theme.dark ? 'accent' : 'secondary'"
              ></v-text-field>
              <v-select
                required
                label="Gender"
                :items="['Male', 'Female']"
                item-color="warning"
                v-model="ownerLogin.gender"
                :color="$vuetify.theme.dark ? 'accent' : 'secondary'"
                prepend-inner-icon="contact_support"
              ></v-select>
            </div>
            <v-btn
              color="warning"
              type="submit"
              small
              depressed
              :loading="nextLoad"
              >Next</v-btn
            >
          </form>
        </div>
        <div
          class="form-completed col-12"
          :class="animatedFinish"
          style="padding: 0"
        >
          <div class="welcome-title d-flex flex-column" style="height: 100%">
            <div class="greeting d-flex flex-column">
              <span>Well, Hello</span>
              <span class="display-1 font-weight-black mt-2">
                {{ ownerLogin.username }}
              </span>
            </div>
            <v-spacer></v-spacer>
            <div class="btn-action d-flex flex-column">
              <v-btn
                color="warning"
                depressed
                small
                class="mt-2"
                @click="formFinish"
                >Finish</v-btn
              >
            </div>
          </div>
        </div>
      </div>
    </section>
    <section class="nav-login d-flex align-center mx-auto" :class="formWidth">
      <div class="divider-one mr-1" style="width: 100%; overflow: hidden">
        <div class="divider-nav"></div>
        <div :class="navigatorOne"></div>
      </div>
      <div class="divider-two ml-1" style="width: 100%; overflow: hidden">
        <div class="divider-nav"></div>
        <div :class="navigatorTwo"></div>
      </div>
    </section>
    <section class="alert-section mt-5">
      <transition
        enter-active-class="animated fadeInUp"
        leave-active-class="animated fadeOutDown"
      >
        <v-alert
          v-if="alert"
          icon="sentiment_dissatisfied"
          dense
          color="warning"
          text
          max-width="220"
          class="mx-auto"
          >Ups.. Try again</v-alert
        >
      </transition>
    </section>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex';

const nameRules = v => (v || '').indexOf(' ') < 0 || 'No Space';

export default {
  name: 'Login',
  components: {
    VAlert: () => import('vuetify/lib')
  },
  data: () => ({
    animatedForm: 'form-load',
    animatedFinish: '',
    nextLoad: false,
    navigatorOne: 'nav-active',
    navigatorTwo: '',
    ownerLogin: {
      username: '',
      gender: ''
    },
    alert: false,
    rules: [nameRules]
  }),
  methods: {
    async submit() {
      this.nextLoad = await true;
      await this.setOwner(this.ownerLogin);
      await new Promise(resolve => setTimeout(resolve, 1000));
      if (this.loginErr) {
        this.alert = await true;
        this.nextLoad = await false;
        await new Promise(resolve => setTimeout(resolve, 3000));
        this.alert = await false;
      } else {
        this.nextLoad = await false;
        this.navigatorOne = await 'nav-leave';
        this.animatedForm = await 'form-next';
        await new Promise(resolve => setTimeout(resolve, 2000));
        this.navigatorTwo = await 'nav-active';
        this.animatedFinish = await 'form-finish';
      }
    },
    formFinish() {
      this.$router.push({ path: 'main' });
    },
    ...mapActions(['setOwner'])
  },
  computed: {
    formWidth() {
      let width = val => {
        switch (val) {
          case 'xs':
            return 'mobile';
          case 'sm':
            return 'small-tablet';
          case 'md':
            return 'tablet';
          case 'lg':
            return 'laptop';
          case 'xl':
            return 'desktop';
        }
      };
      return width(this.$vuetify.breakpoint.name);
    },
    mtLogin() {
      let mt = margin => {
        switch (margin) {
          case 'xs':
          case 'sm':
            return 'mt-5';
          case 'md':
          case 'lg':
            return 'mt-10';
          case 'xl':
            return 'mt-xl';
        }
      };
      return mt(this.$vuetify.breakpoint.name);
    },
    ...mapState(['loginErr', 'owner'])
  }
};
</script>

<style lang="scss" scoped>
#login-section {
  display: flex;
  flex-direction: column;
  align-content: space-around;
  justify-content: center;
  height: 95vh;
}
section.mobile {
  width: 50%;
  overflow: hidden;
}
section.small-tablet {
  width: 30%;
  overflow: hidden;
}
section.tablet {
  width: 20%;
  overflow: hidden;
}
section.laptop {
  width: 15%;
  overflow: hidden;
}
section.desktop {
  width: 10%;
  overflow: hidden;
}
.mt-xl {
  margin-top: 120px;
}
.divider-nav {
  border-bottom: thin solid #02779e;
  display: block;
  width: 100%;
}
// custom slot class
.v-text-field {
  padding-top: 0 !important;
}
</style>
